import React from 'react';
import {Route, Switch} from 'react-router-dom';
import asyncComponent from '../../../../../util/asyncComponent';

const live = ({match}) => (
  <Switch>
      <Route exact from={`${match.url}/`} component={asyncComponent(() => import('./main.js'))}/>
      <Route exact from={`${match.url}/:project_id`} component={asyncComponent(() => import('./SiteSurveyInfo.js'))}/>
      <Route exact from={`${match.url}/:project_id/:room_id/modify`} component={asyncComponent(() => import('./RoomModify.js'))}/>
      <Route exact from={`${match.url}/:project_id/:room_id`} component={asyncComponent(() => import('./RoomInfo.js'))}/>
      <Route exact from={`${match.url}/sticker`} component={asyncComponent(() => import('./EditPhoto.js'))}/>
      <Route component={asyncComponent(() => import('app/routes/extraPages/routes/404'))}/>
  </Switch>
);

export default live;